@import "../../styles/colors.scss";

/* ---------- MEDIA SCREEN -- MOBILE ---------- */
@media (max-width: 600px) {
  header {
    flex-direction: column;
    align-items: center;

    h1 {
      margin-bottom: 10px;
    }

    .header__body {
      width: 100%;
    }
  }
}

/* ---------- MEDIA SCREEN -- TABLET && DESKTOP ---------- */
@media (min-width: 601px) {
  header {
    h1 {
      margin-right: 20px;
    }

    .header__body {
      flex: 1;
    }
  }
}

header {
  display: flex;
  justify-content: flex start;
  padding: 15px;

  h1 {
    color: $text_color_orange;
  }

  .header__body {
    display: flex;
    justify-content: space-between;
  }
}
